import express from 'express';
import { factorial } from 'controllers/factorial';
const app = express();
const route = express.Router();

route.get('/', function (req, res) {
  res.send(`Factorial of 5 is ${factorial(5)}`)
  fact = 1;
})
app.use(route)
app.listen(1000, () => console.log('Server started listening to port 1000'))
